.header {
  position: relative;

  &__wrapper {
    max-width: 1440px;
    margin: 0 auto;
    position: relative;
  }

  &__nav {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    padding-top: 53px;

    z-index: 10;

    color: $color-default-white;

    @include vp-767 {
      padding-top: 20px;
      justify-content: center;
    }
  }
}

.hero.hero--winners {
  padding-bottom: 0;
}
