.promo-card {
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: $color-text;
  flex: 0 0 auto;
  width: 156px;
  min-height: 204px;
  position: relative;

  h4 {
    font-weight: 700;
    font-size: 16px;
    line-height: 126%;
    letter-spacing: -0.02em;
    margin: 0;
  }

  p {
    font-weight: 700;
    font-size: 16px;
    line-height: 126%;
    letter-spacing: -0.02em;
    margin: 5px 0;
  }

  span {
    font-weight: 500;
    font-size: 12px;
    line-height: 150%;
    letter-spacing: -0.02em;
  }

  &--green {
    background-image: $green-to-white;

    h4 {
      color: $color-limeade;
    }
  }

  &--pelorous {
    background-image: $pelorous-to-white;
  }

  &--pale-turquoise {
    background-image: $pale-turquoise-to-white;

    h4 {
      color: $color-pelorous;
    }
  }

  &--fog {
    background-image: $fog-to-white;
  }

  &--picton-blue {
    background-image: $picton-blue-to-white;

    h4 {
      color: $color-picton-blue;
    }
  }

  &--rose {
    background-image: $classic-rose-to-white;
  }

  &--lavender {
    background-image: $lavender-blue-to-white;
  }
}

.promo-card__content {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding-bottom: 74px;
}

.promo-card__item {
  display: flex;
  gap: 9px;
  align-items: center;
}

.promo-card__image {
  position: absolute;
  bottom: 0;
  right: 0;

  img {
    max-width: 100%;
    height: auto;
    display: block;
  }
}
