.btn {
  position: relative;

  display: inline-flex;
  justify-content: center;
  overflow: hidden;

  min-height: 50px;
  padding: 15px 20px;
  border: none;
  border-radius: 12px;

  background-image: linear-gradient(
    to right,
    $color-button 0%,
    $color-button 100%
  );
  color: $color-title;

  font-weight: 700;
  font-size: 18px;
  line-height: 110%;
  letter-spacing: -0.02em;
  text-align: center;

  cursor: pointer;

  z-index: 1;

  transition: background-image $trans-default, color $trans-default;

  user-select: none;

  @include vp-767 {
    background-image: linear-gradient(to right, #ffffff 0%, #ffffff 100%);
    color: $color-title;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 90%;
    height: 100%;
    background: linear-gradient(
      270deg,
      #ffffff 0%,
      #5b80ff 0.01%,
      #7ac5ff 100%
    );
    transform: translateX(-101%);
    transition: transform 0.3s ease;
    z-index: 0;

    @include vp-767 {
      width: 92%;
      transform: translateX(-100%);
      background: linear-gradient(
        270deg,
        #e4eeff 0%,
        #5b80ff 0.01%,
        #7ac5ff 100%
      );
    }
  }

  &:disabled,
  &--disabled {
    background-image: linear-gradient(to right, #fcfcfc 0%, #fcfcfc 100%);
    color: $color-text-block;
    pointer-events: none;
  }

  @include hover-focus {
    color: $color-default-white;

    &::before {
      transform: translateX(0);
    }
  }

  &:active {
    color: $color-default-white;
    background-image: linear-gradient(270deg, #363de8 0%, #359ff1 100%);

    &::before {
      display: none;
    }
  }

  span {
    position: relative;
    z-index: 5;
  }
}
