.footer {
  padding: 25px 0;

  @include vp-767 {
    padding: 20px 0 31px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    gap: 32px;

    flex-wrap: wrap;
    background-color: $color-default-white;

    border-top: 1px solid $color-border;
    border-radius: 40px;
    padding: 31px 30px 40px 30px;

    color: $color-fiord;

    @include vp-767 {
      border-top: 0;
      background-color: transparent;
      padding: 0;
      border-radius: 0;
    }
  }

  &__logo {
    @include vp-767 {
      display: none;
    }
  }

  &__about {
    display: grid;
    grid-template-columns: repeat(2, auto);

    gap: 34px;

    @include vp-767 {
      grid-template-columns: 1fr;
    }
  }

  &__info {
    font-weight: 500;
    font-size: 14px;
    line-height: 129%;

    margin-top: auto;

    @include vp-767 {
      display: none;
    }
  }

  &__links {
    display: flex;
    flex-direction: column;

    gap: 11px;

    @include vp-767 {
      flex-wrap: wrap;
      flex-direction: row;
      column-gap: 12px;
      row-gap: 10px;
      justify-content: start;

      .footer__link--privacy-policy {
        order: 2;
      }

      .footer__link--user-agreement {
        order: 3;
      }

      .footer__link--promotion-rules {
        order: 1;
      }
    }

    a {
      font-weight: 500;
      font-size: 16px;
      line-height: 137%;
      text-decoration: underline;
      text-decoration-skip-ink: none;
      color: $color-fiord;

      @include vp-767 {
        font-weight: 500;
        font-size: 14px;
        line-height: 110%;
        letter-spacing: -0.02em;
        color: #535b62;
        text-decoration: none;
      }

      @include hover-focus {
        color: #989898;
      }

      @include active {
        color: $color-text;
      }
    }
  }
}
