.winners-list {
  padding: 0;
  padding-bottom: 25px;

  @include vp-767 {
    padding: 32px 0 31px;
  }

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  h2 {
    font-weight: 700;
    font-size: 48px;
    line-height: 100%;
    letter-spacing: -0.02em;
    color: $color-text;
    margin: 0;
    margin-bottom: 35px;

    @include vp-1023 {
      font-size: 40px;
    }

    @include vp-767 {
      margin-bottom: 30px;
      font-size: 32px;
      line-height: 120%;
      color: $color-text;
    }
  }

  &__tabs {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    margin-bottom: 50px;

    @include vp-767 {
      gap: 15px;
      flex-direction: column;
      margin-bottom: 30px;
    }
  }

  &__tab {
    width: 100%;
    border-radius: 20px;
    padding: 32px;
    min-height: 102px;

    border: none;
    cursor: pointer;

    background-image: linear-gradient(
      to right,
      $color-default-white 0%,
      $color-default-white 100%
    );

    font-weight: 700;
    font-size: 32px;

    line-height: 120%;
    letter-spacing: -0.02em;
    color: $color-active-button;
    transition: background-color 0.3s;

    &.active,
    &:active {
      background-image: linear-gradient(155deg, #c3e3ff 0%, #ffffff 51%);
    }

    @include hover-focus {
      background-image: linear-gradient(155deg, #c3e3ff 0%, #ffffff 51%);
    }

    @include vp-1279 {
      font-size: 28px;
      min-height: 90px;
    }

    @include vp-1023 {
      font-size: 24px;
      min-height: 75px;
    }

    @include vp-767 {
      border: 1px solid $color-input;
      background-image: linear-gradient(
        155deg,
        $color-input 0%,
        $color-input 51%
      );
      background-color: $color-input;

      border-radius: 12px;
      padding: 14px 30px;
      min-height: 50px;

      font-size: 18px;
      line-height: 110%;

      &.active,
      &:active {
        border: 1px solid $color-active-button;
        background-color: transparent;
        background-image: linear-gradient(
          155deg,
          $color-transparent 0%,
          $color-transparent 51%
        );
      }

      @include hover-focus {
        border: 1px solid $color-active-button;
        background-color: transparent;
        background-image: linear-gradient(
          155deg,
          $color-transparent 0%,
          $color-transparent 51%
        );
      }
    }
  }

  &__dropdown {
    display: flex;
    justify-content: center;
    position: relative;
    margin-bottom: 50px;

    @include vp-767 {
      margin-bottom: 28px;
    }
  }

  &__dropdown-toggle {
    min-width: 417px;
    max-width: 100%;
    min-height: 89px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    gap: 10px;

    border-radius: 20px;
    border: none;
    padding: 30px;
    cursor: pointer;

    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
    letter-spacing: -0.02em;
    text-align: center;
    color: $color-default-white;

    background-color: $color-active-button;

    @include vp-1279 {
      font-size: 20px;
      min-height: 75px;
      padding: 20px;
    }

    @include vp-1023 {
      font-size: 18px;
      min-height: 60px;
      padding: 15px;
    }

    @include vp-767 {
      border-radius: 12px;
      font-size: 16px;
      min-width: 0;
      width: 100%;

      line-height: 126%;
      padding: 15px 18px;

      min-height: 50px;
    }
  }

  &__dropdown-icon {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
  }

  &__dropdown-menu {
    display: none;
    position: absolute;
    top: calc(100% + 20px);

    min-width: 433px;
    max-width: 100%;
    background-color: $color-input;
    border: none;
    border-radius: 20px;
    padding: 30px 10px;
    margin: 0;

    list-style-type: none;
    z-index: 10;

    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: $color-title;

    @include vp-1279 {
      font-size: 20px;
    }

    @include vp-1023 {
      font-size: 18px;
    }

    @include vp-767 {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100vw - 66px);
      max-width: 100%;
      min-width: 0;
      max-height: 80%;
      overflow-y: auto;
      background-color: $color-default-white;

      z-index: 1001;

      font-size: 16px;
      line-height: 126%;
      color: $color-title;

      padding: 20px 10px;
    }
  }

  &__dropdown-menu li {
    cursor: pointer;

    border-radius: 20px;
    padding: 10px 20px;
    min-width: 413px;
    max-width: 100%;
    height: 49px;

    @include vp-767 {
      min-width: 100%;
      height: auto;
    }
  }

  &__dropdown-item.selected,
  &__dropdown-menu li:hover {
    background-color: $color-title;
    color: $color-default-white;
  }

  &__overlay {
    display: none;

    @include vp-767 {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.2);
      z-index: 1000;
    }
  }

  &__table-wrapper {
    border-radius: 40px;
    padding: 32px;

    width: 100%;
    background: $color-default-white;

    @include vp-767 {
      padding: 0;
    }
  }

  &__period {
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: $color-subtitle;

    display: flex;
    justify-content: space-between;
    gap: 20px;
    flex-wrap: wrap;

    margin-bottom: 30px;

    p {
      margin: 0;
    }

    @include vp-767 {
      align-items: center;
      margin-bottom: 26px;
    }
  }

  &__period-week {
    @include vp-1279 {
      font-size: 30px;
    }

    @include vp-1023 {
      font-size: 28px;
    }

    @include vp-767 {
      font-size: 24px;
    }
  }

  &__period-date {
    @include vp-1279 {
      font-size: 24px;
    }

    @include vp-1023 {
      font-size: 20px;
    }

    @include vp-767 {
      font-size: 18px;
      line-height: 110%;
      padding-right: 4px;
    }
  }

  &__grid-container {
    overflow: hidden;
    max-height: 787px;
  }

  &__grid-header,
  &__grid-item {
    display: grid;
    grid-template-columns: 15.3% 18.1% 1fr;
    gap: 20px;

    @include vp-1279 {
      gap: 15px;
      grid-template-columns: 20% 25% 1fr;
    }

    @include vp-1023 {
      gap: 10px;
      grid-template-columns: 25% 30% 1fr;
    }

    @include vp-767 {
      grid-template-columns: 90px 110px 1fr;
    }
  }

  &__grid-header {
    position: sticky;
    top: 0;
    background-color: $color-default-white;
    z-index: 1;
    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
    letter-spacing: -0.02em;
    border-bottom: 1px solid $color-subtitle;
    padding-bottom: 13px;

    @include vp-1279 {
      font-size: 22px;
    }

    @include vp-1023 {
      font-size: 18px;
    }

    @include vp-767 {
      font-size: 18px;
      line-height: 110%;
    }
  }

  &__grid-body {
    display: block;
    max-height: 630px;
    overflow-y: auto;

    scrollbar-width: thin;
    scrollbar-color: rgba($color-active-button, 0.5) transparent;

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: transparent;
    }

    &::-webkit-scrollbar-thumb {
      background-color: rgba($color-active-button, 0.5);
      border-radius: 3px;
      border: 2px solid transparent;
      background-clip: content-box;
    }

    &::-webkit-scrollbar-thumb:hover {
      background-color: rgba($color-active-button, 0.7);
    }
  }

  &__grid-item {
    font-weight: 500;
    font-size: 24px;
    line-height: 133%;
    letter-spacing: -0.02em;
    padding-bottom: 15px;
    align-items: center;

    @include vp-1279 {
      font-size: 22px;
    }

    @include vp-1023 {
      font-size: 18px;
    }

    @include vp-767 {
      font-size: 16px;

      line-height: 135%;
      letter-spacing: -0.02em;
      align-items: start;
      padding-bottom: 16px;
    }

    &:first-child {
      padding-top: 15px;

      @include vp-767 {
        padding-top: 16px;
      }
    }

    &:last-child {
      padding-bottom: 0;
    }
  }

  &__grid-cell {
    overflow: visible;
    text-overflow: initial;
    white-space: normal;
    word-wrap: break-word;
    min-width: 0;
  }

  &__message {
    text-align: center;
    font-size: 24px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: $color-text;
    margin: 0;

    @include vp-767 {
      font-size: 16px;
    }
  }
}

.winners-list__content {
  display: none;
}

.winners-list__content.active {
  display: block;
}
