.hero {
  position: relative;
  overflow: hidden;
  padding: 0 0 50px;

  @include vp-1023 {
    padding: 39px 0;
  }

  @include vp-767 {
    padding: 39px 0 20px;
  }

  &__background {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
    width: 100%;
    max-width: 1440px;
    overflow: hidden;
    transform-origin: top center;
    /*  animation: zoom-in 1.5s ease-out;

    @keyframes zoom-in {
      from {
        transform: translateX(-50%) scaleY(0.9);
      }
      to {
        transform: translateX(-50%) scaleY(1);
      }
    } */

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center top;
    }

    @include vp-767 {
      width: 100%;
    }
  }

  &__wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;

    z-index: 1;
    min-height: 655px;
    padding-top: 143px;

    @include vp-1279 {
      min-height: 0;
    }

    @include vp-1023 {
      padding-top: 100px;
    }

    @include vp-767 {
      padding-top: 0;
      flex-direction: column;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    max-width: 42%;
    position: relative;
    z-index: 2;

    @include vp-1279 {
      max-width: 44%;
    }

    @include vp-1023 {
      max-width: 46%;
    }

    @include vp-767 {
      padding-top: 40px;
      margin: 0 auto;
      max-width: 100%;
    }

    &--winners {
      max-width: 49%;
      padding-top: 58px;

      @include vp-1279 {
        padding-top: 12px;
        max-width: 50%;
      }

      @include vp-1023 {
        max-width: 55%;
      }

      @include vp-767 {
        padding-top: 40px;
        width: 100%;
        max-width: 100%;
      }
    }
  }

  h2 {
    margin: 0 0 40px;
    font-weight: 700;
    font-size: 64px;
    line-height: 109%;
    letter-spacing: -0.02em;
    color: $color-title;

    @include vp-1279 {
      font-size: 56px;
    }

    @include vp-1023 {
      font-size: 50px;
    }

    @include vp-767 {
      font-size: 48px;
      line-height: 100%;
      color: $color-default-white;
      margin: 0 auto -3px;

      br {
        display: none;
      }
    }
  }

  p {
    font-weight: 500;
    font-size: 24px;
    line-height: 133%;
    letter-spacing: -0.01em;
    margin: 0 0 40px;
    color: $color-text;

    @include vp-1279 {
      font-size: 22px;
    }

    @include vp-1023 {
      font-size: 22px;
    }

    @include vp-767 {
      font-size: 18px;
      line-height: 134%;
      letter-spacing: -0.02em;
      margin-bottom: 23px;
      margin-left: -1px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    label {
      margin-bottom: 16px;
    }
  }

  &__checkbox {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  &__button {
    min-width: 327px;
    align-self: flex-start;
    justify-content: center;
    transition: background-image 0.3s ease, color 0.3s ease, opacity 0.3s ease;
    background-image: linear-gradient(to right, #fcfcfc 0%, #fcfcfc 100%);
    position: relative;
    overflow: hidden;
    color: $color-text-block;
    z-index: 1;

    @include vp-767 {
      min-width: 100%;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 90%;
      height: 100%;
      background: linear-gradient(
        270deg,
        #ffffff 0%,
        #5b80ff 0.01%,
        #7ac5ff 100%
      );
      transform: translateX(-101%);
      transition: transform 0.3s ease;
      z-index: 0;

      @include vp-767 {
        width: 92%;
        transform: translateX(-100%);
        background: linear-gradient(
          270deg,
          #e4eeff 0%,
          #5b80ff 0.01%,
          #7ac5ff 100%
        );
      }
    }

    &:disabled {
      background-image: linear-gradient(to right, #fcfcfc 0%, #fcfcfc 100%);
      color: $color-text-block;
      pointer-events: none;
    }

    &.hero__button--active {
      background-image: linear-gradient(
        to right,
        $color-button 0%,
        $color-button 100%
      );
      color: $color-title;

      @include hover-focus {
        color: $color-default-white;

        &::before {
          transform: translateX(0);
        }
      }

      &:active {
        color: $color-default-white;
        background-image: linear-gradient(270deg, #363de8 0%, #359ff1 100%);

        &::before {
          display: none;
        }
      }

      @include vp-767 {
        background-image: none;
        background-color: $color-button-background;
        color: $color-default-white;

        &::before {
          transform: translateX(0);
        }

        &:hover,
        &:focus {
          color: $color-default-white;
        }

        &:active {
          color: $color-default-white;
          background-image: linear-gradient(270deg, #363de8 0%, #359ff1 100%);

          &::before {
            display: none;
          }
        }
      }
    }

    span {
      position: relative;
      z-index: 5;
    }
  }

  &__image-desktop {
    display: block;
    width: 654px;
    margin-right: -44px;
    margin-top: -143px;
    animation: slide-in 1s ease-out;

    @keyframes slide-in {
      from {
        opacity: 0;
        transform: translate(55px, -25px);
      }

      to {
        opacity: 1;
        transform: translate(0, 0);
      }
    }

    @include vp-1279 {
      margin-right: 0;
      width: 50%;
    }

    @include vp-1023 {
      width: 50%;
      margin-right: 20px;
      margin-bottom: 100px;
    }

    @include vp-767 {
      display: none;
    }

    img {
      width: 654px;
    }
  }

  &__image-mobile {
    display: none;

    @include vp-767 {
      display: block;
      position: relative;
      width: calc(100% + 50px);
      margin: 0 -25px -6px;
      text-align: center;
      animation: fade-scale 0.6s ease-out;

      @keyframes fade-scale {
        from {
          opacity: 0;
          transform: scale(0);
        }

        to {
          opacity: 1;
          transform: scale(1);
        }
      }

      img {
        width: 100%;
        height: auto;
        display: inline-block;
      }
    }
  }

  &--winners {
    .hero__background {
      left: 53.3%;

      @include vp-1023 {
        left: 60%;
      }

      @include vp-767 {
        left: 50%;
      }
    }

    .hero__wrapper {
      @include vp-1023 {
        min-height: 590px;
      }

      @include vp-767 {
        min-height: 0;
      }
    }

    .hero__image-desktop {
      margin-right: -69px;
      margin-left: -17px;

      @include vp-1279 {
        margin-top: -150px;
        margin-right: -44px;
        margin-left: 0;
      }

      @include vp-1023 {
        margin-top: -150px;
        margin-right: 20px;
      }
    }

    .hero__image-mobile {
      @include vp-767 {
        margin-top: 0;
      }
    }

    h2 {
      margin-bottom: 45px;

      @include vp-767 {
        margin-bottom: -3px;
      }
    }
  }
}
