.phone-search-form {
  max-width: 540px;
  min-height: 244px;
  border-radius: 40px;
  padding: 32px;
  background-color: $color-default-white;

  @include vp-767 {
    max-width: 100%;
    min-height: 0;
    padding: 0;
    background-color: transparent;
  }

  h3 {
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: $color-text;
    margin: 0;

    @include vp-1279 {
      font-size: 28px;
    }

    @include vp-1023 {
      font-size: 26px;
    }

    @include vp-767 {
      font-size: 24px;
      margin-top: 1px;
    }
  }

  &__input-wrapper {
    position: relative;
  }

  &__input {
    width: 100%;
  }

  &__submit {
    position: absolute;
    right: 19px;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    cursor: pointer;
    width: 40px;
    height: 40px;

    @include vp-767 {
      width: 30px;
      height: 30px;
    }

    svg {
      @include vp-767 {
        width: 20px;
        height: 20px;
      }
    }
  }

  &__icon {
    display: block;
    position: absolute;
    top: 50%;
    left: 29%;
    transform: translate(-50%, -50%);
    transition: opacity 0.3s ease;

    @include vp-767 {
      left: 44%;
    }
  }

  &__icon--search {
    svg {
      display: block;
      width: 30px;
      height: 30px;
      margin-top: 2px;

      @include vp-767 {
        width: 20px;
        height: 20px;
      }
    }
  }

  &__icon--close {
    opacity: 0;
    width: 40px;
    height: 40px;

    @include vp-767 {
      width: 20px;
      height: 20px;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100%;
      height: 3px;
      background-color: $color-active-button;
      transition: background-color 0.3s ease;
      stroke-width: 3px;
      stroke-linecap: round;
    }

    &::before {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }

  &__submit.is-clear-mode {
    .phone-search-form__icon--search {
      opacity: 0;
    }

    .phone-search-form__icon--close {
      opacity: 1;
    }
  }

  p.phone-search-form__hint {
    font-weight: 500;
    font-size: 16px;
    line-height: 135%;
    letter-spacing: -0.02em;
    text-align: center;
    color: $color-subtitle;
    margin: 0;

    @include vp-767 {
      text-align: start;
    }
  }

  p.phone-search-form__error {
    display: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 135%;
    letter-spacing: -0.02em;
    text-align: center;
    color: $color-error;
    margin: 0;

    @include vp-767 {
      text-align: start;
    }
  }

  &.is-error {
    .phone-search-form__input input {
      color: $color-error;
    }

    .phone-search-form__submit {
      .phone-search-form__icon--search svg {
        path {
          fill: $color-error;
        }
      }

      .phone-search-form__icon--close {
        &::before,
        &::after {
          background-color: $color-error;
        }
      }
    }
  }
}
