.weekly {
  padding: 65px 0 30px;

  @include vp-1023 {
    padding: 30px 0;
  }

  @include vp-767 {
    padding: 10px 0 12px;
    overflow: hidden;
  }

  .container {
    @include vp-767 {
      padding-left: 24px;
      padding-right: 24px;
    }
  }

  h2 {
    font-weight: 700;
    font-size: 48px;
    line-height: 100%;
    letter-spacing: -0.02em;
    margin: 0 0 35px;
    color: $color-text;

    @include vp-1023 {
      font-size: 40px;
      margin: 0 0 25px;
    }

    @include vp-767 {
      font-size: 32px;
      line-height: 120%;
      margin: 0 0 19px;
    }
  }
}

.weekly__wrapper {
  display: flex;
  gap: 40px;
  flex-wrap: wrap;

  @include vp-1279 {
    gap: 20px;
  }

  @include vp-1023 {
    flex-direction: column;
  }

  @include vp-767 {
    gap: 0;
  }
}

.weekly__container {
  display: flex;
  flex-direction: column;
  border-radius: 40px;
  padding: 32px 31px;
  background-color: $color-default-white;
  width: fit-content;
  max-width: 100%;

  @include vp-767 {
    border-radius: 20px;
    background-color: $color-transparent;
    background-image: $alice-blue-to-white;
    margin: 0 -24px;
    margin-bottom: 8px;
    padding: 13px 24px;
    width: calc(100% + 48px);
    max-width: none;

    &--more-than-6-months {
      padding-top: 9px;
    }

    &--from-3-to-6-months {
      margin-bottom: 10px;
    }
  }

  h3 {
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: $color-picton-blue;
    margin: 0 -13px 23px 0;

    @include vp-1279 {
      font-size: 28px;

      & br {
        display: none;
      }
    }

    @include vp-767 {
      font-size: 24px;
      line-height: 120%;
      letter-spacing: -0.02em;
      color: $color-picton-blue;
      margin: 0 0 15px 0;
    }
  }

  &--less-than-3-months h3 {
    color: $color-viking;
  }

  &--from-3-to-6-months h3 {
    color: $color-picton-blue;
  }

  &--more-than-6-months h3 {
    color: $color-cornflower-blue;
  }
}

.weekly__sliders {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  width: 100%;

  @include vp-767 {
    flex-wrap: nowrap;
    overflow: visible;
  }
}

.weekly__swiper {
  .swiper {
    @include vp-767 {
      width: 100%;
      overflow: visible;
    }
  }

  .swiper-wrapper {
    display: flex;
    gap: 30px;

    flex-wrap: wrap;

    @include vp-1023 {
      flex-wrap: wrap;
    }

    @include vp-767 {
      gap: 0;
      flex-wrap: nowrap;
    }
  }

  .swiper-slide {
    flex-shrink: 0;
  }

  @include vp-767 {
    .weekly__sliders {
      .swiper-wrapper {
        display: flex;
      }
    }
  }
}
