.custom-input,
.custom-textarea {
  position: relative;

  display: flex;
  width: 100%;
  padding: 20px 0;

  @media screen and (max-width: 767px) {
    padding: 14px 0 16px;
    width: 100%;
  }

  label {
    display: flex;
    align-items: center;
    width: 100%;

    @media screen and (max-width: 767px) {
      align-items: flex-start;
      flex-direction: column;
    }

    input,
    textarea {
      font-weight: 700;
      font-size: 32px;

      line-height: 120%;
      letter-spacing: -0.02em;
      color: #0088fe;

      flex-grow: 1;
      width: 100%;
      min-height: 80px;

      padding: 19px 50px 19px 25px;

      border: 1px solid transparent;
      border-radius: 20px;
      background: $color-input;
      outline: none;

      transition: border 0.3s ease;

      @include vp-1279 {
        min-height: 80px;
        font-size: 28px;
      }

      @include vp-1279 {
        min-height: none;
        font-size: 24px;
      }

      @include vp-767 {
        font-size: 18px;
        line-height: 110%;
        min-height: 60px;
      }

      @media screen and (max-width: 767px) {
        max-width: unset;
        width: 100%;
      }
    }
  }

  &__label {
    @media screen and (max-width: 767px) {
      margin: 0 0 18px 0;
    }
  }

  &__error {
    position: absolute;
    right: 0;
    bottom: 35px;

    color: #ff1553;

    opacity: 0;

    transition: opacity 0.3s ease;

    @media screen and (max-width: 767px) {
      bottom: 20px;
    }
  }

  &.is-invalid {
    input,
    textarea {
      border: 2px solid #ff1553;

      &:focus,
      &:hover {
        border: 2px solid rgba(#ff1553, 0.5);
      }
    }
  }

  &.is-valid {
    input {
      border: 2px solid #22b259;

      &:focus,
      &:hover {
        border: 2px solid rgba(#22b259, 0.5);
      }
    }
  }

  .input-message {
    position: absolute;
    right: 0;
    bottom: -5px;

    &.is-invalid {
      color: #ff0000;
    }

    &.is-valid {
      color: #15ab07;
    }
  }
}
