.participation {
  padding: 44px 0 75px;

  @include vp-1023 {
    padding: 50px 0;
  }

  @include vp-767 {
    padding: 10px 0;
  }

  .container {
    @include vp-767 {
      padding-left: 24px;
      padding-right: 24px;
    }
  }
}

.participation__container {
  background-color: $color-default-white;
  border-radius: 40px;
  display: flex;
  padding: 40px 40px 50px;
  min-height: 400px;
  position: relative;

  @include vp-1023 {
    flex-direction: column;
    padding: 50px;
    min-height: 0;
  }

  @include vp-767 {
    padding: 0;
  }
}

.participation__image-desktop {
  position: absolute;
  top: -79px;
  left: -50px;
  max-width: 634px;
  width: 60%;
  z-index: 1;

  img {
    width: 100%;
    height: auto;
    object-fit: contain;
  }

  @include vp-1023 {
    position: static;
    width: 100%;
    margin: -135px auto 0;
    display: flex;
    justify-content: center;

    img {
      max-width: 100%;
    }
  }

  @include vp-767 {
    display: none;
  }
}

.participation__content {
  display: flex;
  width: 48.6%;
  margin-left: auto;
  flex-direction: column;
  justify-content: center;

  @include vp-1023 {
    width: 100%;
    margin-left: 0;
    margin-top: -80px;
    align-items: center;
  }

  @include vp-767 {
    margin: 0;
    align-items: flex-start;
  }
}

.participation__content h2 {
  margin: 0 0 20px;
  font-weight: 700;
  font-size: 48px;
  line-height: 100%;
  letter-spacing: -0.02em;
  color: $color-text;

  @include vp-1023 {
    font-size: 40px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 120%;
  }
}

.participation__content p {
  margin: 0;
  font-weight: 500;
  font-size: 24px;
  line-height: 133%;
  letter-spacing: -0.01em;
  color: $color-text;

  @include vp-1023 {
    font-size: 28px;
    text-align: center;
  }

  @include vp-767 {
    font-size: 16px;
    letter-spacing: -0.02em;
    line-height: 138%;
    text-align: start;
  }
}
