.logo {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  transition: opacity $trans-default;

  @include vp-767 {
    justify-content: center;
  }

  &:not(.logo--index):not(.logo--footer) {
    @media (hover: hover), screen and (min-width: 0\0) {
      @include hover-focus {
        opacity: 0.8;
      }
    }

    @include focus {
      opacity: 0.8;
    }

    @include active {
      opacity: 0.6;
    }
  }
}

.logo--footer {
  width: 56px;
  height: 40px;

  @include vp-767 {
    display: none;
  }
}

.logo img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
