.results {
  padding: 25px 0;

  @include vp-767 {
    padding: 9px 0 0;
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 32px;

    @include vp-767 {
      margin-bottom: 98px;
    }

    label {
      margin-bottom: 16px;
    }
  }

  &__checkbox {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  &__button {
    min-width: 327px;
    align-self: center;
    justify-content: center;
    transition: background-image 0.3s ease, color 0.3s ease, opacity 0.3s ease;
    background-image: linear-gradient(to right, #fcfcfc 0%, #fcfcfc 100%);
    position: relative;
    overflow: hidden;
    color: $color-text-block;
    z-index: 1;

    @include vp-767 {
      min-width: 100%;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 90%;
      height: 100%;
      background: linear-gradient(
        270deg,
        #ffffff 0%,
        #5b80ff 0.01%,
        #7ac5ff 100%
      );
      transform: translateX(-101%);
      transition: transform 0.3s ease;
      z-index: 0;

      @include vp-767 {
        width: 92%;
        transform: translateX(-100%);
        background: linear-gradient(
          270deg,
          #e4eeff 0%,
          #5b80ff 0.01%,
          #7ac5ff 100%
        );
      }
    }

    &:disabled,
    &--disabled {
      background-image: linear-gradient(to right, #fcfcfc 0%, #fcfcfc 100%);
      color: $color-text-block;
      pointer-events: none;
    }

    &.results__button--active {
      background-image: linear-gradient(
        to right,
        $color-button 0%,
        $color-button 100%
      );
      color: $color-title;

      @include hover-focus {
        color: $color-default-white;

        &::before {
          transform: translateX(0);
        }
      }

      &:active {
        color: $color-default-white;
        background-image: linear-gradient(270deg, #363de8 0%, #359ff1 100%);

        &::before {
          display: none;
        }
      }

      @include vp-767 {
        background-image: none;
        background-color: $color-button-background;
        color: $color-default-white;

        &::before {
          transform: translateX(0);
        }

        &:hover,
        &:focus {
          color: $color-default-white;
        }

        &:active {
          color: $color-default-white;
          background-image: linear-gradient(270deg, #363de8 0%, #359ff1 100%);

          &::before {
            display: none;
          }
        }
      }
    }

    span {
      position: relative;
      z-index: 5;
    }
  }

  &__container {
    position: relative;
    border-radius: 40px;
    overflow: hidden;
    display: flex;
    align-items: stretch;
    background-color: $color-default-white;
    min-height: 316px;

    @include vp-767 {
      flex-direction: column;
      padding: 0;
      text-align: center;
      overflow: visible;
      border-radius: 0;
      background-color: transparent;
      min-height: 0;
    }
  }

  &__content {
    flex: 1;
    padding: 40px 0 40px 38px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include vp-767 {
      order: 2;
      padding: 50px 0 20px;
      width: 100%;
      z-index: 3;
    }
  }

  h2 {
    margin: 0 0 40px;
    font-weight: 700;
    font-size: 48px;
    line-height: 100%;
    letter-spacing: -0.02em;
    color: $color-text;

    @include vp-1279 {
      font-size: 40px;
    }

    @include vp-767 {
      font-size: 24px;
      color: $color-default-white;
      margin-bottom: 23px;

      br {
        display: none;
      }
    }
  }

  &__button-winners {
    max-width: 327px;
    margin-bottom: 10px;

    @include vp-767 {
      max-width: 100%;
    }
  }

  &__image-wrapper {
    position: relative;
    width: 57.7%;

    @include vp-767 {
      position: absolute;
      width: calc(100% + 48px);
      margin-left: -24px;
      margin-right: -24px;
      order: 1;
      min-height: 257px;
      overflow: visible;
      z-index: -1;
    }
  }

  &__background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: left center;
    }

    @include vp-767 {
      border-radius: 20px 20px 0 0;
      overflow: hidden;
      background-color: $color-viking;
      z-index: -1;

      img {
        height: auto;
        min-height: 257px;
      }
    }
  }

  &__trophy {
    position: absolute;
    z-index: 2;
    right: 15%;
    top: 50%;
    transform: translateY(-50%);

    @include vp-767 {
      top: -66px;
      bottom: auto;
      right: 50%;
      transform: translateX(50%);
      z-index: 4;
    }

    img {
      max-width: 357px;
      height: auto;

      @include vp-1023 {
        width: 300px;
      }

      @include vp-767 {
        width: 89px;
      }
    }
  }
}
